
@import url('https://fonts.googleapis.com/css2?family=Staatliches&display=swap');

html, body {
    margin: 0;
    padding: 0;
    height: 100%;
    max-width: 100%;
    overflow-x: hidden;
    min-height: 100%;
}
#wrapper {
    font-family: 'Open Sans';
    font-size: 16px;
    letter-spacing: 0.025em;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    //color: #ffffff;
}
.overhided {
    height: 100px;
    overflow: hidden;
    background-color: transparent;
}
.title {
    color: #333333;
    text-align: center;
    font-size: 30px;
    font-weight: 700;
    letter-spacing: 0.05em;
    margin: 0 auto; 
}
header {
    position: fixed;
    top: 0;
    width: 100%;
    box-sizing: border-box;
    z-index: 9999;
}
.headsection {
    display: block;
    width: 100%;
    height: 100px;
    align-items: center;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.4);
}
.headin1220 {
    position: relative;
    padding: 0px 15px;
    width: 1220px;
    margin: 0 auto;
}
.headcont {
    width: 1220px;
    display: flex;
}
.logo {
    width: 80px;
    height: 80px;
    margin-right: auto;
}
.contacts p {
    margin: 0;
}

.menu {
    display: flex;
    justify-content: center;
    padding: 0;
    margin:  auto 0;
    text-align: center;
    align-items: center;
    
}

.menu li {
    list-style: none;
    height: 40px;
    display: flex;
    align-items: center;
}

.menu li:last-child {
    background-color: #337ab7;
    border-radius: 20px;
    vertical-align: middle;
}
.menu li:last-child .menu__item {
    color: #ffffff; 

}

.menu__item {

    font-size: 16px;
    font-weight: 400;
    letter-spacing: 0.025em;
    color: #666666;
    text-decoration: none;
    margin: 0 20px;

    
}

.menu a:hover {
    border-bottom: 2px solid #ffffff;
}

.navig ul {
    margin: auto auto;
}

.main {
    flex-grow: 1;

}

.content__wrapper {
    background-color: #ffffff;
    width: 100%;
}

.form__wrapper {
    background-color: #f7f7f7;
    margin-top: 100px;
}
.forminfo {
    width: 1220px;
    display: flex;
    margin-top: 55px;
    padding: 55px 15px 30px 15px;
    margin: 0 auto;
}
.info {
    display: block;
    width: 540px;
    margin-right: auto;
    text-align: center;
    padding-left: 40px;
    margin-top: auto;
    margin-bottom: 85px;
    
}
.info__title {
    font-size: 35px;
    margin: 0 0 30px 0;
    letter-spacing: 0.032em;
    color: #333333;
}
.info__text {
    width: 500px;
    margin: 0;
    padding: 0;
    color: #818181;
}

.forms {
    display: block;
    margin-left: auto;
}



.popup-main {
    /*margin: 35px 0 25px 0;
    margin-bottom: 25px;*/
    display: flex;
    flex-direction: column;
    /* align-items: center;*/
}

.popup-title {
    margin-bottom: 30px;
    height:  15px;
}

.form__title {
    font-size: 23px;
    line-height: 18px;
    font-weight: 700;
    color: #535353;
    margin: 0;
}

.popup-close {
    display: flex;
    width: 27px;
    height: 27px;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: -27px;
    top: -27px;
    background-color: #777777;
    border: none;
}

.popupcloseimg {
    display: block;
    width: 15px;
    height: 15px;
}

.popup-close a {
    margin: 0;
    text-decoration: none;
}

.myform {
    display: flex;
    flex-direction: column;
    padding: 0px;
    margin: 0 auto;
}

.myform .form-group {
    padding-bottom: 2px;
}
.labelreq {
    display: flex;
    text-align: center;
    vertical-align: middle;
}
.label__rq {
    height: 7px;
    margin: auto 0 auto 2px;
}
.form__label {
    display: block;
    margin: 0 0 7px 0;
    font-size: 16px;
    line-height: 1.2;
    font-weight: 400;
    color: #535353;
    height: 16px;
}


.form__input {
    display: block;
    margin: 0 auto;
    width: 568px;
    height: 50px;
    background-color: #ffffff;
    border: 1px solid #c3c3c3;
    padding: 0 10px;
    margin-bottom: 10px;
    border-radius: 4px;
}
.form__input:focus {
    border: 2px solid #66afe9;
    box-shadow: 0 0 15px #66afe9;
    outline-style: none;

}

.form__item ._error {
    box-shadow: inset 0 0 15px #fac3c3;
    border: 1px solid #fd9898;
    font-family: 'PT Sans';
    font-size: 14px;
    line-height: 1.2;
    font-weight: 400;
    font-style: italic;
    color: #dc7f7f;
}

.form__submit  {
    width: 370px;
    height: 50px;
    color: #ffffff;
    background-color:#337ab7;
    border: 0;
    text-transform: uppercase;
    border-radius: 24px;
    margin: 0 auto;
}
.form__submit:focus {
    box-shadow: 10px 5px 5px rgba(0, 0, 0, 0.4);
}
.checkbox {
    display: flex;
    margin-bottom: 35px;
}
.checkbox ._error {
    box-shadow: inset 0 0 15px #fac3c3;
    border: 1px solid #fd9898;
}
.form__label a {
    color: #3498db;
    text-decoration: none;
}






