.blockquote-8 {
    position: relative;
    padding: 16px 0px;
    margin: 16px 46px;  
    font-size: 16px;
    text-align: center;
    margin: 0 auto;
    overflow: hidden;
    //background-color: #f8f8f8;
    width: 980px;
    margin-bottom: 105px;
}


.blockquote-8:before,
.blockquote-8:after {
    position: absolute;
    color: #c4c4c4;
    font-size: 100px;
    font-family: 'Staatliches', sans-serif;    
    height: 100px;    
}
.blockquote-8:before {
    content: '“';
    left: 0px;
    top: -20px;
}
.blockquote-8:after {
    content: '”';
    right: 0px;
    bottom: 40px;
}
.blockquote-8 cite {
    color: #337AB7;
    font-size: 15px;
    padding-top: 10px;
    display: block;
}
